.details-card-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    padding: 10px;
    box-shadow: 0 14px 14px rgba(0, 0, 0 0.12), 0 1px 1px rgba(0, 0, 0, 0.24);
    transition: all 0.3s cubic-bezier(0.165, 0.84, 0.44, 1);
    
}

.details-card-container:hover {
    box-shadow: 0 14px 28px rgba(0, 0, 0 0.12), 0 10px 10px rgba(0, 0, 0, 0.24);
}

.card-name {
    font-size: 30px;
    margin-bottom: -15px;
}

.card-value {
    font-size: 51px;
    font-weight: 600;
    margin: 5px;
}